import React, {useEffect} from 'react'
import { connect } from 'react-redux'
import {useLocation} from 'react-router-dom'
import Navbar from '../components/Navbar'
import {chackAuthenticated, load_user, googleAuthenticated} from '../actions/auth'
import queryString from 'query-string'

const Layout = props => {
    const location = useLocation()
    useEffect(()=>{
        const values = queryString.parse(location.search)
        const state = values.state? values.state:null;
        const code = values.code? values.code:null;

        console.log('State' + state)
        console.log('Code' + code)

        if(state && code){
            props.googleAuthenticated(state, code)
            // props.facebookAuthenticated(state, code)
        }else{
            props.chackAuthenticated()
            props.load_user()
        }
    },[location])

    return (
        <div>
            <Navbar/>
            {props.children}
        </div>
    )
}

export default connect(null,{
    chackAuthenticated, load_user, googleAuthenticated
})(Layout)
