import React from 'react'
import {Route, Switch } from 'react-router-dom'
import Home from './contsiners/Home';
import Login from './contsiners/Login';
import Signup from './contsiners/Signup';
import Layout from './hocs/Layout';
import ResetPassword from './contsiners/ResetPassword'
import ResetPasswordConfirm from './contsiners/ResetPasswordConfirm'
import Activate from './contsiners/Activate';
import Facebook from './contsiners/Facebook';
import Google from './contsiners/Google';

const App =(props)=>{
  return(
    <Layout>
      <Switch>
        <Route exact path='/' component={Home}/>
        <Route exact path='/login' component={Login}/>
        <Route exact path='/signup' component={Signup}/>
        <Route exect path='/facebook' component={Facebook}/>
        <Route exect path='/google' component={Google}/>
        <Route exact path='/reset-password' component={ResetPassword}/>
        <Route exact path='/password/reset/confirm/:uid/:token' component={ResetPasswordConfirm}/>
        <Route exact path='/activate/:uid/:token' component={Activate}/>
      </Switch>
    </Layout>
  )
}

export default App;
